.product {
   width: 100%;
   height: 500px;
   padding: 15px;
}

.products_title {
   font-size: 20px;
   font-weight: 600;
   line-height: 24.38px;
   margin: 15px;
}

.trending_list {
   display: flex;
   column-gap: 20px;
   justify-content: center;
}

.product_link {
   border: 1px solid #272771;
   box-shadow: 0 2px 4px #272771;
   border-radius: 5px;
   transition: transform 0.3s ease-in;
}

.product_link:hover {
   transform: scale(0.9);
}
